html {
  --color-accent: #3f1ca0;
  --color-highlight: #936dfd;
  background: #083575;
  height: 100%;
}

body, input, button {
  font: 11pt/1.4 Lato, "Fira Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

body {
  --main-padding: 20px;
  max-width: 860px;
  margin: 40px auto;
  padding: 10px var(--main-padding);
  background-color: white;
  border-radius: 21px;
  box-shadow: 0 0 40px rgb(0, 0, 0, .5);
}

button, input:not([type=checkbox]) {
  height: 2em;
  border: 1px solid #ccc;
  box-sizing: border-box;
  border-radius: 10px;
  background: #eee;
}

button {
  font-weight: bold;
  cursor: pointer;
}
button:hover, button:active {
  color: white;
  background: #936dfd;
  background: var(--color-highlight);
}
button:active {
  background: #3f1ca0;
  background: var(--color-accent);
}

a {
  color: #3f1ca0;
  color: var(--color-accent);
}
a:hover {
  color: #936dfd;
  color: var(--color-highlight);
}

header {
  display: relative;
  color: white;
  text-shadow: 2px 2px 2px var(--color-accent);
  background: var(--color-accent) calc(100% - var(--main-padding)) no-repeat url(https://solid.github.io/ldflex-playground/static/media/solid.a22f9cc3.svg);
  border-radius: 20px 20px 0 0;
  margin: calc(-1 * var(--main-padding));
  padding: var(--main-padding);
}
header h1 {
  margin: .25em 0;
}
header p {
  display: inline;
  margin-right: 1em;
}
header a {
  color: white;
}
header a:hover {
  color: var(--color-highlight);
}

footer {
  display: flex;
  align-items: baseline;
}
footer, footer * {
  font-size: .95em;
}

footer p {
  margin-bottom: 0.25em;
}
footer p:first-child {
  flex-grow: 1;
}

button.like:before {
  content: "\1F44D\A0";
}

form.playground h3 {
  margin: 1em 0 .25em;
}
form.playground p,
form.playground pre,
form.playground ul {
  margin: 0;
  padding: 0 0 0 1em;
  list-style: none;
}

form.playground p.expression {
  display: flex;
  align-items: center;
  padding: 0 0 .5em;
}
form.playground p.expression label,
form.playground p.expression input {
  font-family: monospace !important;
}
form.playground p.expression input,
form.playground p.expression button {
  box-sizing: border-box;
}
form.playground p.expression input {
  flex-grow: 1;
  margin-right: 1ex;
}

form.playground p.multiple {
  font-style: italic;
  text-align: right;
}

form.playground p.single [data-error]:before {
  content: 'Expression error: ' attr(data-error);
  font-style: italic;
  font-weight: bold;
  color: red;
}

main {
  padding-top: 1em;
}

